.footer {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  padding: 1rem 3rem;
  max-width: 800px;
  margin-left: auto;
  margin-right: auto;
  color: #fff;
  // column-gap: 2rem;

  .copyright {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-content: center;
    justify-content: center;
    align-self: center;
    font-size: 0.85rem;
    p {
     display: flex;
     flex-direction: column;
     .my-user{
        color: green;
     }
      span {

        text-align: center;
        display: flex;
        align-self: center;
        color: #8c12f0;
        font-weight: 800;
      }
    }
  }
  .followMe {
    display: flex;
    align-items: center;
    gap: 1rem;

    h4 {
      font-size: 0.85rem;
    }
    .stick {
      height: 2px;
      width: 3rem;
      background: #fff;
    }

    .social_icons {
      display: flex;
      gap: 0.5rem;

      svg {
        opacity: 0.7;
        cursor: pointer;
        &:hover {
          opacity: 1;
        }
      }
    }
  }
}
